"use client";

import * as React from "react";
import { formatDistanceToNow } from "date-fns";
import { cn } from "@/lib/utils";
import { Badge } from "@/components/ui/badge";
import { ScrollArea } from "@/components/ui/scroll-area";
import { type Mail } from "../data";

interface MailListProps {
  items: Mail[];
  selectedMail: Mail | null;
  onSelectMail: (mail: Mail) => void;
}

export function MailList({
  items = [],
  selectedMail,
  onSelectMail,
}: MailListProps) {
  const safeItems = React.useMemo(() => {
    return items && Array.isArray(items) ? items : [];
  }, [items]);

  if (safeItems.length === 0) {
    return (
      <div className="p-4 text-center text-sm text-muted-foreground">
        No emails found
      </div>
    );
  }

  return (
    <ScrollArea className="h-full">
      <div className="flex flex-col">
        {safeItems.map((item) => (
          <button
            key={item.id}
            className={cn(
              "relative flex flex-col items-start gap-1 border-b p-4 text-left transition-colors hover:bg-muted/50",
              selectedMail?.id === item.id && "bg-muted",
              !item.read &&
                "before:absolute before:left-0 before:top-0 before:h-full before:w-1 before:bg-blue-600",
            )}
            onClick={() => onSelectMail(item)}
          >
            <div className="flex w-full items-center justify-between gap-2">
              <span className="text-sm font-semibold">{item.from}</span>
              <span className="shrink-0 text-xs text-muted-foreground">
                {formatDistanceToNow(new Date(item.date), { addSuffix: true })}
              </span>
            </div>

            <div className="text-xs font-medium text-muted-foreground">
              {item.subject}
            </div>

            <div className="line-clamp-2 text-xs text-muted-foreground">
              {item.message.substring(0, 180)}...
            </div>

            {item.labels && item.labels.length > 0 && (
              <div className="mt-1 flex flex-wrap items-center gap-1">
                {item.labels.map((label) => (
                  <Badge
                    key={label}
                    variant="outline"
                    className="rounded-sm border-muted-foreground/40 bg-muted/40 px-1.5 py-0 text-[10px] font-normal text-muted-foreground"
                  >
                    {label}
                  </Badge>
                ))}
              </div>
            )}

            {!item.read && (
              <span className="absolute right-4 top-4 flex h-2 w-2 rounded-full bg-blue-600" />
            )}
          </button>
        ))}
      </div>
    </ScrollArea>
  );
}
