"use client";

import * as React from "react";
import { ChevronDown, Triangle } from "lucide-react";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { accounts as accountsData } from "../data";

// Default account in case the accounts data is undefined
const DEFAULT_ACCOUNTS = [
  {
    label: "Alicia Koch",
    email: "alicia@example.com",
    icon: null,
  },
];

interface AccountSwitcherProps {
  isCollapsed: boolean;
}

export function AccountSwitcher({ isCollapsed }: AccountSwitcherProps) {
  const [open, setOpen] = React.useState(false);

  // Safe accounts data with fallback
  const accounts = React.useMemo(() => {
    return accountsData && accountsData.length > 0
      ? accountsData
      : DEFAULT_ACCOUNTS;
  }, []);

  const [selectedAccount, setSelectedAccount] = React.useState<
    (typeof accounts)[0]
  >(accounts[0]);

  if (isCollapsed) {
    return (
      <Button variant="ghost" size="icon" className="h-9 w-9">
        <Triangle className="h-4 w-4 rotate-180" />
      </Button>
    );
  }

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>
        <Button
          variant="ghost"
          role="combobox"
          aria-expanded={open}
          aria-label="Select account"
          className="flex w-full items-center justify-between px-3 py-2 text-base font-medium"
        >
          <div className="flex items-center gap-2">
            <Triangle className="h-4 w-4 rotate-180" />
            <span>{selectedAccount.label}</span>
          </div>
          <ChevronDown className="h-4 w-4 shrink-0 opacity-50" />
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-[200px] p-0" align="start">
        <div className="p-2">
          {accounts.map((account) => (
            <Button
              key={account.email}
              variant="ghost"
              className="w-full justify-start"
              onClick={() => {
                setSelectedAccount(account);
                setOpen(false);
              }}
            >
              {account.label}
            </Button>
          ))}
        </div>
      </PopoverContent>
    </Popover>
  );
}
